footer {
  width: 100%;
  background-color: #1c1b1e;
  display: flex;
  justify-content: center;
  padding: 4% 8%;
  margin-top: 100px;
}
footer > div:first-of-type {
  padding-left: 0;
}
footer > div {
  padding-left: 60px;
  padding-right: 60px;
}
.footer-heading {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 16px;
  color: white;
  opacity: 0.9;
  letter-spacing: 1px;
  margin: 16px 0;
}
.footer-link {
  text-decoration: none;
  color: white;
  display: block;
  margin-top: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 14px;
  opacity: 0.8;
}
@media screen and (max-width: 500px) {
  footer {
    flex-direction: column;
  }
  footer > div {
    padding-left: 0;
    margin-bottom: 20px;
  }
}
