@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Open Sans", sans-serif;
}

#topbar {
  width: 100%;
  box-shadow: 0 2px 5px #ccc;
  background-color: white;
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 8%;
}
.sidebar {
  display: flex;
  flex-direction: column;
  background-color: white;
  box-shadow: 0 2px 5px #ccc;
  min-width: 320px;
  min-height: 50vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  padding: 20px 8%;
  -webkit-transform: translateX(-100%) translateY(55px);
          transform: translateX(-100%) translateY(55px);
  transition: -webkit-transform 200ms;
  transition: transform 200ms;
  transition: transform 200ms, -webkit-transform 200ms;
}
.sidebar-active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  -webkit-transform: translateY(55px);
          transform: translateY(55px);
}
#search-wrapper-hidden {
  margin-bottom: 25px;
}
.sidebar > a {
  text-decoration: none;
  color: black;
  margin-bottom: 5px;
  font-weight: 600;
}
hr {
  margin-bottom: 20px;
}
#left-section {
  display: inline-flex;
  justify-content: start;
  align-items: center;
}
#bars {
  display: none;
  cursor: pointer;
}
#bars div {
  width: 25px;
  height: 3px;
  background-color: rgb(27, 25, 25);
  margin: 2px;
  transition: all 0.3s ease;
}
.toggle .line1 {
  -webkit-transform: rotate(-45deg) translate(-5px, 6px);
          transform: rotate(-45deg) translate(-5px, 6px);
}
.toggle .line2 {
  opacity: 0;
}
.toggle .line3 {
  -webkit-transform: rotate(45deg) translate(-4px, -6px);
          transform: rotate(45deg) translate(-4px, -6px);
}
#logo {
  font-size: 36px;
  font-weight: 400;
  margin-right: 16px;
}
#logo > a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}
#logo span {
  font-weight: 800;
}
#left-section > a {
  text-decoration: none;
  color: black;
  margin-left: 32px;
  font-weight: 600;
}
#search-wrapper,
#search-wrapper-hidden {
  width: 100%;
  max-width: 400px;
  position: relative;
}
#search-wrapper > i,
#search-wrapper-hidden > i {
  position: absolute;
  color: #999;
  font-size: 18px;
  top: 50%;
  left: 18px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
#search-box,
#search-hidden {
  width: 100%;
  border: 1px solid rgb(245, 242, 242);
  background-color: rgb(245, 242, 242);
  box-sizing: border-box;
  padding: 10px 24px 8px 48px;
  color: black;
  outline: none;
}
#right-section {
  display: inline-flex;
  justify-content: end;
  align-items: center;
}
#cart-wrapper {
  position: relative;
  margin-right: 32px;
}
#cart-count {
  width: 18px;
  height: 18px;
  font-size: 12px;
  margin: 0;
  text-align: center;
  font-weight: 600;
  background-color: #009688;
  color: white;
  border-radius: 50%;
  position: absolute;
  top: -12px;
  right: -12px;
}
#cart-wrapper > a {
  text-decoration: none;
  color: black;
}
#right-section > img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
@media screen and (max-width: 500px) {
  #topbar {
    padding: 10px 4%;
  }
  #left-section > a {
    display: none;
  }
  #search-wrapper {
    display: none;
  }
  #logo {
    font-size: 26px;
  }
  #right-section > img {
    width: 25px;
    height: 25px;
  }
  #cart-wrapper {
    margin-right: 20px;
  }
  #cart-wrapper > a {
    font-size: 15px;
  }
  #cart-count {
    width: 12px;
    height: 12px;
    top: -6px;
    right: -8px;
    font-size: 8px;
  }
  #bars {
    display: flex;
    flex-direction: column;
    font-size: 36px;
    margin-right: 5px;
  }
  .sidebar {
    display: flex;
    width: 200px;
    height: 50vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;
  }
}

.product-card {
  width: 100%;
  max-width: 19%;
  margin-left: 0.5%;
  margin-right: 0.5%;
  margin-bottom: 16px;
  box-sizing: border-box;
  box-shadow: 0 2px 5px #ccc;
  border-radius: 5px;
}
.product-card > a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}
.product-image {
  width: 100%;
}
.product-details {
  box-sizing: border-box;
  padding: 16px;
}
.product-details > h4 {
  font-weight: 600;
  margin: 0;
  font-size: 16px;
}
.product-details > h5 {
  font-weight: 600;
  opacity: 0.8;
  margin: 0;
  margin-top: 12px;
}
.product-details > p {
  margin: 0;
  margin-top: 12px;
  font-weight: 600;
  color: #009688;
}
@media screen and (max-width: 500px) {
  .product-card {
    max-width: 80%;
    margin-left: 3%;
  }
}

.car {
  margin-top: 100px;
  margin-left: 5%;
}

.homepage-section {
  padding: 4% 8% 0;
}
.section-heading {
  font-size: 28px;
  font-weight: 600;
  opacity: 0.8;
  margin: 28px 0;
}
.product-wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

footer {
  width: 100%;
  background-color: #1c1b1e;
  display: flex;
  justify-content: center;
  padding: 4% 8%;
  margin-top: 100px;
}
footer > div:first-of-type {
  padding-left: 0;
}
footer > div {
  padding-left: 60px;
  padding-right: 60px;
}
.footer-heading {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 16px;
  color: white;
  opacity: 0.9;
  letter-spacing: 1px;
  margin: 16px 0;
}
.footer-link {
  text-decoration: none;
  color: white;
  display: block;
  margin-top: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 14px;
  opacity: 0.8;
}
@media screen and (max-width: 500px) {
  footer {
    flex-direction: column;
  }
  footer > div {
    padding-left: 0;
    margin-bottom: 20px;
  }
}

#main-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 4%;
  margin-top: 100px;
}
#tick-wrapper {
  width: 200px;
  height: 200px;
  background-color: #49d049;
  border-radius: 50%;
  padding: 40px;
  box-sizing: border-box;
  -webkit-animation: popup 1s;
          animation: popup 1s;
}
#tick-wrapper img {
  width: 100%;
}
.main-heading {
  font-size: 36px;
  color: rgba(0, 0, 0, 0.8);
  font-weight: 600;
  margin-top: 48px;
  margin-bottom: 16px;
}
.section-heading {
  font-size: 20px;
  color: rgba(0, 0, 0, 0.8);
  font-weight: 400;
  margin-bottom: 16px;
}
@-webkit-keyframes popup {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1.25, 1.25);
            transform: scale(1.25, 1.25);
  }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
}
@keyframes popup {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1.25, 1.25);
            transform: scale(1.25, 1.25);
  }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
}
@media screen and (max-width: 500px) {
  .main-heading,
  .section-heading {
    text-align: center;
  }
}

#main-section {
  padding: 2% 8%;
  margin-top: 100px;
}
#main-heading {
  font-size: 36px;
  font-weight: 500;
  margin: 0;
  margin-bottom: 48px;
}
#content-wrapper {
  display: flex;
  justify-content: start;
}
#card-list-left {
  width: 50%;
}
.section-heading {
  font-size: 24px;
  color: rgba(0, 0, 0);
  font-weight: 400;
  margin: 0;
  margin-bottom: 16px;
}
#card-list-right {
  width: 50%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: 48px;
  margin-left: 3%;
  box-shadow: 0 2px 5px #ccc;
  border-radius: 5px;
  padding: 16px;
}
p {
  margin: 16px 0;
}
#amount-count {
  color: black;
  opacity: 0.8;
  font-size: 24px;
  font-weight: 800;
  margin-left: 4px;
}
#btn-place-order {
  background-color: #009688;
  font-size: 18px;
  border: 0;
  cursor: pointer;
  outline: none;
  padding: 18px 36px;
  border-radius: 6px;
  margin-top: 36px;
  font-weight: 600;
  color: white;
}
.checkout-card {
  display: flex;
  box-shadow: 0 2px 5px #ccc;
  border-radius: 5px;
  background-color: white;
  max-width: 600px;
  padding: 16px;
  margin-bottom: 16px;
}
.checkout-img {
  width: 80px;
  min-width: 80px;
  border-radius: 5px;
  margin-right: 16px;
}
.checkout-card h4 {
  font-weight: 600;
  margin-bottom: 4px;
  font-size: 18px;
}
.checkout-card p {
  margin-top: 4px;
  margin-bottom: 4px;
}
@media screen and (max-width: 500px) {
  #content-wrapper {
    flex-direction: column;
  }
  #card-list-left {
    width: 100%;
  }
  #card-list-right {
    width: 100%;
    margin-left: 0;
  }
}

#product-wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 2% 8%;
  box-sizing: border-box;
  margin-top: 100px;
}
#product-image {
  width: 40%;
  padding-right: 8%;
  align-self: flex-start;
}
.image-wrapper {
  display: block;
}
#image-preview {
  width: 100%;
}
#product-details {
  align-self: flex-start;
  width: 60%;
  box-sizing: border-box;
  z-index: 5;
}
.product-title {
  font-size: 48px;
  font-weight: 500;
}
.product-brand {
  font-weight: 600;
  color: black;
  opacity: 0.6;
  margin: 0;
  margin-top: 8px;
}
.section-heading {
  font-size: 24px;
  color: rgba(0, 0, 0);
  margin-top: 36px;
  font-weight: 400;
  margin-bottom: 16px;
}
.product-price {
  max-width: 500px;
  font-size: 24px;
  letter-spacing: 1px;
  line-height: 1.3;
  font-weight: 600;
  color: #009688;
  margin: 0;
}
.description {
  max-width: 500px;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 1.3;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.6);
}
#product-images {
  display: flex;
}
#product-images img {
  width: 50px;
  border-radius: 6px;
  margin: 0 8px;
  cursor: pointer;
  border-radius: 5px;
  padding: 4px;
}
.active-image {
  box-sizing: content-box;
  border: 2px solid #009688;
}
#btn-cart {
  background-color: #009688;
  font-size: 18px;
  border: 0;
  cursor: pointer;
  outline: none;
  padding: 18px 36px;
  border-radius: 6px;
  margin-top: 36px;
  font-weight: 600;
  color: white;
  transition: -webkit-transform 200ms;
  transition: transform 200ms;
  transition: transform 200ms, -webkit-transform 200ms;
}
.bigger {
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
}
@media screen and (max-width: 500px) {
  #product-wrapper {
    flex-direction: column;
    padding-left: 30px;
    padding-right: 30px;
  }
  #product-image {
    width: 100%;
  }
  #product-details {
    width: 100%;
  }
  .product-title {
    font-size: 32px;
  }
  .product-brand {
    font-size: 22px;
  }
  .section-heading {
    margin-top: 20px;
  }
  #product-images {
    flex-wrap: wrap;
  }
}

